import {
  ref
} from "vue";

export const langList = [
  {
    id: "en",
    label: "E",
  },
  {
    id: "zh",
    label: "华"
  },
  {
    id: "jp",
    label: "あ"
  },
] as const;

export type LangId = (typeof langList)[number]["id"];

export const currentLangId = ref<LangId>(langList[0].id);

export function setCurrentLangId(langId: LangId) {
  currentLangId.value  = langId;
}

export function getCurrentLangId(): LangId {
  return currentLangId.value;
}
