import { 
  type LangId,
  getCurrentLangId
} from "./lang-helper"
import { pageRecord } from "./page-helper";

export interface HomeViewCard {
  title: string,
  root?: string,
}

export interface HomeViewCircle {
  title: string,
  description: string,
  email: string,
}

export interface HomeViewImgSrc {
  logo: string,
  pencil: string,
}

export interface AssetConfigHomeView {
  imgSrc: HomeViewImgSrc,
  title: string,
  description: string,
  cardList: Array<HomeViewCard>,
  circleInfo: HomeViewCircle,
}

export interface AppScopeConfig {
  logo: string,
}

export interface HomeViewConfig {
  homeView: AssetConfigHomeView,
  appScope: AppScopeConfig,
}


export interface AssetConfigDetailView {
  banner: string[],
  title: string,
  description: string,
  imgList: string[],
  articleImgList: string[],
}

let currentPath = "/";
let currentCardIndex = NaN;
let homeViewConfig = null as null | Record<LangId, HomeViewConfig>;
const cardRecord = {} as Record<`card-${number}`, Record<LangId, AssetConfigDetailView>>;

export function getCurrentCardIndex() {
  return currentCardIndex;
}

export function setCurrentPath(routePath: string) {
  currentPath = routePath;
}
export function setCurrentCardIndex(cardIndex: number) {
  currentCardIndex = cardIndex;
}

export function getHomeViewConfig() {
  return homeViewConfig;
}

export function getCurrentHomeViewConfig() {
  return homeViewConfig![getCurrentLangId()];
}

export function setHomeViewConfig(config: Record<LangId, HomeViewConfig>) {
  homeViewConfig = config;
}

export function updateCardRecordOf(cardIndex: number, cardInfo: Record<LangId, AssetConfigDetailView>) {
  cardRecord[`card-${cardIndex}`] = cardInfo;
}

export function getCardRecordOf(cardIndex: number): Record<LangId, AssetConfigDetailView> | null {
  return cardRecord[`card-${cardIndex}`] || null;
}

export function getAssetConfig() {
  const langId = getCurrentLangId();
  if (currentPath === pageRecord.home.path) {
    if (homeViewConfig) {
      return {
        homeView: homeViewConfig[langId].homeView,
        detailView: null,
      };
    }
  }
  if (currentPath === pageRecord.detail.path) {
    if (!isNaN(currentCardIndex)) {
      return {
        homeView: homeViewConfig ? homeViewConfig[langId].homeView : null,
        detailView: cardRecord[`card-${currentCardIndex}`][langId]
      }
    }
  }
  return null;
}

