import { 
  createRouter,
  createWebHashHistory,
} from "vue-router";
import {
  routeList
} from "./route-list";
import { pageRecord } from "@/utils/page-helper";
import { getCardRecordOf, getHomeViewConfig, setCurrentCardIndex, setCurrentPath, setHomeViewConfig, updateCardRecordOf } from "@/utils/config-helper";
import { loadAllDetailViewConfigOf, loadAllHomeViewConfig } from "@/api/axios-helper";
import { isShowAppLoading } from "@/utils/app-helper";

const router = createRouter({
  history: createWebHashHistory(),
  routes: routeList,
});

router.beforeEach(async (to, from, next) => {
  setCurrentPath(to.path);
  const homeViewConfig = getHomeViewConfig();
  if (!homeViewConfig) {
    isShowAppLoading.value = true;
    await loadAllHomeViewConfig().then((obj) => {
      isShowAppLoading.value = false;
      setHomeViewConfig(obj);
    }).catch((err) => {
      console.error(err);
    });
  }
  if (to.path === pageRecord.home.path) {
    next();
    return;
  }
  if (to.path === pageRecord.detail.path) {
    const cardIndex = parseInt(to.query.index as string);
    setCurrentCardIndex(cardIndex);
    const cardViewConfig = getCardRecordOf(cardIndex);
    if (cardViewConfig) {
      next();
      return;
    }
    isShowAppLoading.value = true;
    const cardReply = await loadAllDetailViewConfigOf(cardIndex);
    isShowAppLoading.value = false;
    updateCardRecordOf(cardIndex, cardReply);
    next();
    return;
  }
  next();
});

export default router;
