import { 
  langList,
  type LangId
 } from "@/utils/lang-helper";
import * as NSAxios from "axios";
import {
  type AssetConfigDetailView,
  type HomeViewConfig,
} from "../utils/config-helper";


const apiHelper = NSAxios.default.create({  
});

export {
  apiHelper,
};

export function loadHomeViewConfig(lang: LangId): Promise<HomeViewConfig> {
  return apiHelper.get(`${process.env.BASE_URL}config-assets/home-view/text/${lang}.json`).then((res) => {
    return res.data;
  });
}

export function loadAllHomeViewConfig() {
  return Promise.all(langList.map((item) => {
    return loadHomeViewConfig(item.id);
  })).then((valueList) => {
    const recordOne = {} as Record<LangId, HomeViewConfig>;
    valueList.forEach((valueItem, valueIndex) => {
      recordOne[langList[valueIndex].id] = valueItem;
    });
    return recordOne;
  });
}

export function loadDetailViewConfig(cardIndex: number, lang: LangId) {
  return apiHelper.get(`${process.env.BASE_URL}config-assets/detail-view/card-${cardIndex}/text/${lang}.json`).then((res) => {
    return res.data;
  });
}

export function loadAllDetailViewConfigOf(cardIndex: number): Promise<Record<LangId, AssetConfigDetailView>> {
  return Promise.all(langList.map((item) => {
    return loadDetailViewConfig(cardIndex, item.id);
  })).then((valueList) => {
    const recordOne = {} as Record<LangId, AssetConfigDetailView>;
    valueList.forEach((valueItem, valueIndex) => {
      recordOne[langList[valueIndex].id] = valueItem;
    });
    return recordOne;
  });
}
