
import { defineComponent } from "vue"; 

export default defineComponent({
  name: "GlobalLoading",
  components: {
  },
  props: {
    isShow: {
      type: Boolean,
      default: () => {
        return false;
      },
    },
  },
  setup() {
    return {};
  },
  data: () => {
    return {
      isShowOverlay: false,
    };
  },
  computed: {
  },
  watch: {
    isShow: {
      handler() {
        this.isShowOverlay = this.isShow;
      },
      immediate: true,
    },
  },
  methods: {
  },
});

