import { pageRecord } from "@/utils/page-helper";
import { 
  type RouteRecordRaw
} from "vue-router";

export const routeList: Array<RouteRecordRaw> = [
  {
    path: pageRecord.home.path,
    name: "home",
    component: () => import("@/views/HomeView/HomeView.vue"),
  },
  {
    path: pageRecord.detail.path,
    name: "detail",
    component: () => import("@/views/DetailView/DetailView.vue"),
  },
];